import React from 'react';
import "./Skills.scss"
import {MdRestaurant} from "react-icons/md";

const Skills = () => {
    return (
        <div className="Skills">
            <div className="title">
                <h3>
                    <span className="logo"><MdRestaurant size="40" /> <b>Skill</b></span>
                </h3>
            </div>
            <div className="category">
                <div className="item languages">
                    <div className="category_title"><h3>Front-End</h3></div>
                    <div className="iconBox"><img src="imgs/html5.png" alt="HTML5" /></div>
                    <div className="iconBox"><img src="imgs/css3.png" alt="CSS5" /></div>
                    <div className="iconBox"><img src="imgs/javascript.png" alt="Javascript" /></div>
                    <div className="iconBox"><img src="imgs/jQuery.png" alt="jQuery" /></div>
                    <div className="iconBox"><img src="imgs/react.png" alt="React" /></div>
                </div>
                <div className="item databases">
                    <div className="category_title"><h3>Back-End</h3></div>
                    <div className="iconBox"><img src="imgs/java.png" alt="Java" /></div>
                    <div className="iconBox"><img src="imgs/mysql.png" alt="Mysql" /></div>
                    <div className="iconBox"><img src="imgs/maria.png" alt="Maria" /></div>
                    <div className="iconBox"><img src="imgs/mssql.png" alt="Mssql" /></div>
                    <div className="iconBox"><img src="imgs/mongoDB.png" alt="MongoDB" /></div>
                </div>
                <div className="item server">
                    <div className="category_title"><h3>Devops</h3></div>
                    <div className="iconBox"><img src="imgs/tomcat.png" alt="Tomcat" /></div>
                    <div className="iconBox"><img src="imgs/git.png" alt="GIT" /></div>
                    <div className="iconBox"><img src="imgs/docker.png" alt="Docker" /></div>
                </div>
            </div>            
        </div>
    )
};

export default Skills;