import React from "react";
import "./AboutMe.scss";
import {MdAccountBox, MdDriveFileRenameOutline, MdHome, MdOutlineEmail, MdLightbulb} from "react-icons/md";



const AboutMe = () => {
    return (
        <div className="AboutMe">
            <div className="title">
                <h3>
                    <span className="logo"><MdAccountBox size="40" /> <b>About Me</b></span>
                </h3>
            </div>
            <div className="contents">
                <div className="list">
                    <span className="logo"><MdDriveFileRenameOutline size="28" /> Name</span>
                    <p>박신우</p>
                </div>
                <div className="list">
                    <span className="logo"><MdHome size="28" /> Address</span>
                    <p>서울시 구로구</p>
                </div>
                <div className="list">
                    <span className="logo"><MdOutlineEmail size="28" /> E-mail</span>
                    <p>myhappyman@naver.com</p>
                </div>
                <div className="list">
                    <span className="logo"><MdLightbulb size="28" /> Position</span>
                    <p>웹 개발자(Full Stack)</p>
                </div>
            </div>
        </div>
    );
}

export default AboutMe;