import {useState, useEffect} from "react";
import PropTypes from 'prop-types';
import TopMenuItem from "./TopMenuItem";
import "./TopMenu.scss";

const TopMenu = ({menuList}) => {
    
    const [hoverArr, setHoverArr] = useState([]);
    
    useEffect(()=>{
        const newArr = [];
        menuList.forEach((m, i) => {
            newArr.push(false);
        });
        setHoverArr(newArr);
    }, [menuList]);

    const onMouseEnter = (event) => {
        const key = parseInt(event._targetInst.key);
        const newArr = [];
        menuList.forEach((m, i) => {
            if(i === key) newArr.push(true);
            else newArr.push(false);
        });
        setHoverArr(newArr);
    };
    const onMouseLeave = (event) => {
        const newArr = [];
        menuList.forEach((m, i) => {
            newArr.push(false);         
        });
        setHoverArr(newArr);
    };

    return (
        <div className="TopMenu">
            <ul>
                {
                    menuList.map((menu, i) => (
                        <li
                            key={i}
                            className={hoverArr[i] ? "Menu animate" : "Menu"}
                            onMouseEnter={onMouseEnter}
                            onMouseLeave={onMouseLeave}
                        >
                            <TopMenuItem name={menu.page_name} />
                        </li>
                    ))
                }
            </ul>
        </div>
    );
};
TopMenu.prototypes = {
    menuList: PropTypes.array.isRequired
}

export default TopMenu;