import {MdMenuBook} from "react-icons/md";
import "./Record.scss";

const Record = () => {
    return (
        <div className="Record">
            <div className="title">
                <h3>
                    <span className="logo"><MdMenuBook size="40" /> <b>Record</b></span>
                </h3>
            </div>
            <div className="record">
                <div className="record_area">
                    <div className="record_title"><h3>Blog, Github 운영</h3></div>
                    <div className="contents">
                        <p>우리는 수 많은 프로젝트들을 수행하는 과정에서 다양한 종류의 이슈들을 직면합니다.</p>
                        <p>이런 이슈를 해결하기 위해 다양한 방식(구글링, 책 등)으로 해결을 하게 되는데,</p>
                        <p>기록하지 않은 이슈들 대부분은 시간이 지남에 따라 잊혀지고 같은 이슈가 재발할 경우</p>
                        <p>이전과 비슷한 과정을 통해 해결방법을 찾는 불상사가 발생하고 이는 개발하는데 불필요한 시간을 사용합니다.</p>
                        <p>이런 불편을 해소하고자 기록의 중요성을 느끼고 스스로에게 자산이 되는 블로그를 시작하게 되었습니다.</p>
                        <p>네이버 블로그를 시작하여 현재는 티스토리에 정착하여 하나 둘 써나간 글이 200개 이상의 글들이 되었습니다.</p>
                        <p>버그 해결, 기술 경험, 명령어 정리 등 남기고자 하는 내용을 기록하고 있습니다.</p>
                    </div>
                </div>
                <div className="record_div_area">
                    <div className="record_div">
                        <div className="contents">
                            <div className="content_left"><h3>Blog</h3></div>
                            <div className="content_right">
                            <a href="https://myhappyman.tistory.com/" target="blank"><img src="imgs/blog.png" alt="blog" /></a>
                                <a href="https://myhappyman.tistory.com/" target="blank">https://myhappyman.tistory.com/</a>
                            </div>
                        </div>
                    </div>
                    <div className="record_div">
                        <div className="contents">
                            <div className="content_left"><h3>Github</h3></div>
                            <div className="content_right">
                                <a href="https://github.com/myhappyman" target="blank"><img src="imgs/github.png" alt="github" /></a>
                                <a href="https://github.com/myhappyman" target="blank">https://github.com/myhappyman</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Record;